<template lang="pug">
  div#joinus
    div.container
      h1.joinus__title.section__title {{ serviceKo }} 
      p.section__subtitle
        | {{ serviceKo }}    .
        br
        |   ,   .
        br
        span.joinus__title--copyEmail(
          @click="toast"
          v-clipboard:copy="mailTo"
          title="   "
        )   
          i.far.fa-copy

      div.global__cta
        a.global__cta--link(
          :href="`mailto:${mailTo}?subject= () `"
        )
          button.global__cta--btn.mailto(
            title="  ( )"
          )  

        router-link.global__cta--link(
          :to="'/sellerform'"
        )
          button.global__cta--btn.form(
            title=""
          ) 
</template>

<script>
import { globalVar } from '@/globalVar'

export default {
  name: 'joinus',

  data () {
    return {
      mailTo: globalVar.mailTo,
      serviceKo: globalVar.serviceKo,
      serviceEn: globalVar.serviceEn,
      baseURI: globalVar.ceoWebsiteUrl,
      companyMailTo: globalVar.companyMailTo,
      sellerRequestUrl: globalVar.sellerRequestUrl,
    }
  },

  methods: {
    toast () {
      this.$toasted.show('  ', {
        theme: "primary",
        position: "bottom-center",
        duration : 2000
      })
    }
  },

}
</script>



